@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: linear-gradient(#8bc4d6, #e6edf0) no-repeat center center fixed;
  background-size: cover;
  font-family: "Montserrat", sans-serif;
}

/* HOMEPAGE */

.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.home-container {
  display: flex;
  justify-content: center;
  margin-top: 100px;
  align-items: center;
}

.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 550px;
}

.about h2 {
  color: #f4f4f4;
  font-size: 40px;
  text-shadow: 1px 1px 0px #d5e6eb;
}

.about p {
  color: #4995a6;
  font-size: 20px;
  background-color: rgb(239, 250, 255, 0.2);
  border-radius: 25px;
  padding: 60px;
}

.home-info {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 370px;
  align-items: center;
  background-color: rgb(194, 221, 230, 0.4);
  height: 400px;
  border-radius: 25px;
}

.home-info input {
  padding: 10px;
  border: none;
  margin: 5px;
  width: 295px;
  border-radius: 15px;
}

.home-info p {
  color: #7babb9;
}

.logo {
  display: flex;
  margin: 20px 100px 50px 0;
  width: 500px;
}

.signin {
  display: flex;
  align-items: center;
  position: absolute;
  width: 300px;
  top: 150px;
  right: 50%;
  margin-right: -435px;
  margin-top: 10px;
}

.signin button {
  position: absolute;
  top: 90px;
  right: 50%;
  margin-right: -53px;
}

.barra {
  margin-top: 150px;
}

/* INPUTS E BOTOES */

button:focus {
  box-shadow: 0 0 0 0;
  outline: 0;
}

input:focus,
select:focus {
  box-shadow: 0 0 0 0;
  background-color: #dbeaef;
  outline: 0;
}

.botao-laranja {
  background-color: #f8a978;
  color: #f4f4f4;
  border-radius: 25px;
  padding: 5px 15px;
  font-size: 15px;
  border: 3px solid #f8a978;
  margin: 20px 0;
  transition: 0.2s ease-in-out;
}

.botao-laranja:hover {
  color: #fc9c60;
  background: none;
}

.botao-azul {
  color: #f4f4f4;
  background-color: #8bc4d6;
  border-radius: 25px;
  padding: 5px 15px;
  font-size: 15px;
  border: 3px solid #8bc4d6;
  transition: 0.2s ease-in-out;
}

.botao-azul:hover {
  color: #69aec4;
  background: none;
}

/* ESTRUTURA */

.structure {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.structure h2 {
  color: #f4f4f4;
  font-size: 40px;
  text-shadow: 1px 1px 0px #d5e6eb;
  letter-spacing: 2px;
}

.structure img {
  height: 170px;
  width: 320px;
  margin: 0 40px;
}

.structure-imgs {
  display: flex;
  justify-content: center;
  margin: 60px 0 150px 0;
  background-color: rgb(239, 250, 255, 0.2);
  padding: 50px 30px;
  border-radius: 25px;
  width: 1150px;
}

/* CLIENTES */

.pet-img {
  width: 150px;
  border-radius: 5px;
  -webkit-filter: grayscale(90%);
          filter: grayscale(90%);
  transition: 0.3s ease-in-out;
}

.pet-img:hover {
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}

.client-pets {
  display: flex;
  justify-content: center;
}

.client-pet {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(239, 250, 255, 0.2);
  border-radius: 25px;
  padding: 40px 20px 30px 20px;
  margin: 50px 20px 0 20px;
  height: 150px;
  width: 150px;
}

.client-pet p {
  color: #4995a6;
  font-size: 15px;
}

.clients {
  margin-bottom: 100px;
}

.clients h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #f4f4f4;
  font-size: 40px;
  text-shadow: 1px 1px 0px #d5e6eb;
}

/* FOOTER */

.footer {
  background-color: rgb(194, 221, 230, 0.4);
  width: 100%;
}

.footer p {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #8bc4d6;
  margin-top: 20px;
}

.footer-link {
  text-decoration: none;
  margin: 20px 30px;
  color: #579cb1;
  font-weight: bold;
  transition: 0.5s ease-in-out;
}

.footer-links {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.footer-link:hover {
  color: #0f4555;
}

.nav {
  background-color: rgb(194, 221, 230, 0.4);
  height: 50px;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
}

.nav-content {
  display: flex;
  justify-content: flex-end;
  text-decoration: none;
  border: none;
}

.nav p {
  margin-right: 50px;
  color: white;
}

.hidden {
  display: none;
}

.show {
  display: block;
}

.container {
  display: flex;
  justify-content: center;
  margin: 0 0;
}

.cadastro {
  flex-direction: column;
  width: 540px;
  background-color: rgb(194, 221, 230, 0.4);
  padding: 50px;
  border-radius: 25px;
  height: 450px;
  margin-top: 40px;
}

.cadastro-pet {
  height: 500px;
  margin-top: 20px;
  padding: 20px 50px 50px 50px;
}

.cadastro input {
  padding: 10px 0 10px 15px;
  border: none;
  margin: 5px;
  width: 515px;
  border-radius: 15px;
}

.botoes-cadastro {
  display: flex;
  align-items: baseline;
  justify-content: center;
}

.botoes-cadastro button {
  margin: 40px 70px;
}

.cadastro h2 {
  color: #4995a6;
}

.select {
  color: grey;
  padding: 10px 0 10px 15px;
  border: none;
  margin: 5px;
  width: 529px;
  border-radius: 15px;
}

input[type="file"] {
  display: none;
}
label {
  padding: 10px 0 10px 15px;
  width: 105px;
  background-color: rgb(255, 255, 255);
  font-size: 13px;
  color: grey;
  display: block;
  margin: 5px;
  cursor: pointer;
  border-radius: 15px;
  font-family: "Arial", sans-serif;
}

.dash-container {
  display: flex;
  justify-content: center;
  margin: 30px 0;
}

.dashboard {
  display: flex;
  flex-direction: row;
  background-color: rgb(194, 221, 230, 0.4);
  line-height: 0.5;
  width: 800px;
  border-radius: 25px;
  padding: 30px;
}

.dashboard img {
  width: 150px;
}

.dashboard h2 {
  color: #f4f4f4;
  font-size: 25px;
  text-shadow: 0.5px 1px 0px #d5e6eb;
  letter-spacing: 2px;
  margin-bottom: 25px;
}

.dashboard h4 {
  color: #4995a6;
}

.dashboard p {
  color: #4995a6;
  font-size: 14px;
}

/* ABOUT */

.sobre-humano {
  width: 400px;
  margin-bottom: 100px;
}

.sobre-pet {
  width: 400px;
}

/* AGENDA */

.agendamentos {
  margin-bottom: 140px;
}

